@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;500&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Noto Sans JP', sans-serif;
    background: white;
}


/* HEADER STYLES */
header {
    position: absolute;
    top: 0;
    height: 100px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}

.header-logo img {
    height: 60px;
}

.header-nav {
    display: flex;
    align-items: center;
    z-index: 100;
}

.header-nav ul {
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-nav ul li {
    display: block;
    height: 50px;
}

.nav-link, .profile-button {
    display: flex;
    text-decoration: none;
    color: white;
    margin-left: 30px;
    height: 100%;
    font-size: 1.2rem;
    transition: transform 0.2s;
    cursor: pointer;
}

.profile-button .MuiSvgIcon-root {
    font-size: 3rem;
}

.nav-link:hover, .profile-button:hover {
    transform: scale(1.05);
}

.header-icon {
    height: 40px;
}


/* SUBNAV STYLES */
header .profile-button {
    outline: none;
    border: none;
    background-color: rgba(0, 0, 0, 0);
}

.subnav {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    background-color: #333;
    box-shadow: 1px 1px 5px black;
    border-bottom-left-radius: 8px;

    padding: 30px 0 30px 0;
    right: 0;
    top: 0;
    z-index: 200;
}

.subnav-header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    text-align: right;
    padding: 20px;
    min-width: 300px;
}

.subnav-header h2 {
    color: white;
}

.subnav-header .profile-circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 40px;
    background-color: #ef6c00;
    color: white;
}

.subnav h5 {
    color: #666;
}

.subnav ul {
    width: 100%;
}

.subnav li {
    list-style: none;
    text-align: right;
    display: block;
}

.subnav li:hover {
    background-color: #555;
}

.subnav li a {
    text-decoration: none;
    color: white;
    display: block;
    height: 100%;
    width: 100%;
    padding: 10px 0 10px 0;


    font-size: 1.2rem;
    transition: transform 0.2s;
    cursor: pointer;
}

.subnav li a.logout {
    color: #ef6c00;
}

.subnav li button {
    background: none;
    border: none;
    height: 100%;
}

.subnav-login {
    text-decoration: none;
    color: #ef6c00;
    padding: 0 5px 0 5px;
    cursor: pointer;
}

.subnav .close-button, .subnav-header h1, .subnav li a {
    padding-right: 30px;
    padding-left: 30px;
}


/* LOGOUT + LOGIN PAGE STYLES */
#logout, #login {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
}

.logout-container, .login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 70vh;
    width: 70vw;
}

#logout img, #login img {
    width: 400px;
}

#logout h1, #logout h2, #login h2 {
    color:#ef6c00;
}

#login h1, #logout h3, #login h3 {
    color: white;
}

#logout a, #login a {
    text-decoration: none;
}


/* MISC */
.post-time {
    padding: 10px;
    font-weight: 100;
    font-style: italic;
    font-size: 0.8rem;
}

.black {
    color: black;
}

.username, .orange {
    color: #ef6c00;
}

.white {
    color: white;
}


/* MOBILE MEDIA QUERIES */
@media screen and (max-width: 500px) {
    #logout, #login {
        font-size: 0.8rem;
        text-align: center;
    }

    #logout img, #login img {
        width: 90vw;
    }

    .subnav {
        width: 100vw;
        font-size: 1.2rem;
    }

    .subnav ul {
        margin-top: 30px;
    }

    .subnav li {
        height: 80px;
    }

    .subnav ul button {
        width: 100vw;
        text-align: right;
    }

    .subnav li a {
        font-size: 1.8rem;
    }
}
#home {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    background-color: grey;
    position: absolute;
    height: 100%;
    width: 100%;
    bottom: 0;
}

.things, .place {
    color: white;
    display: flex;
    justify-content: center;
}

/* LOGIN SLIDER */
.register, .login {
    width: 70%;
}
.login-slide {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50vw;
    height: 100%;
    background-color: #333;
    box-shadow: 1px 1px 5px black;
    animation: slideInLeft;
    animation-duration: 0.3s;
}

@keyframes slideInLeft {
    from {left: -1000px}
    to {left: 0}
}

.login-slide h2 {
    margin-bottom: 20px;
}

.intro {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 50%;
}

.intro img {
    width: 40vw;
    max-width: 500px;
}

.intro .vertical-button-container, .login-form .vertical-button-container {
    display: flex;
    flex-direction: column;
}

.login-form {
    width: 100%;
}

.login-form .MuiFormLabel-root {
    color: #ef6c00;
}

.login-form .MuiInputBase-root {
    color: #ef6c00;
}


/* ARTWORK + MARKETPLACE CTA SECTION */
.artwork-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
    overflow-y: hidden;
}

.artwork-container img {
    width: 95%;
    margin-bottom: 20px;
}

.artwork-container button {
    margin-top: 20px;
}

.artwork-container a {
    text-decoration: none;
}


/* MOBILE MEDIA QUERIES */
@media screen and (max-width: 768px) {
    #home {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, 1fr);
        justify-content: center;
        align-items: center;
        position: relative;
        height: 100%;
    }

    .login-slide {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        background-color: #333;
        z-index: 50;
    }

    .artwork-container {
       overflow-y: visible;
       position: relative;
    }

    .intro {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 60%;
    }

    .intro img {
        width: 60vw;
        margin-bottom: 30px;
    }

    .intro .vertical-button-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .artwork-container {
        height: 100%;
        padding: 50px 0 50px 0;
    }
}
#marketplace {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
    padding: 30px;
    margin-top: 100px;
}

.marketplace-container, .post-board {
    grid-column: 1;
}

h1 {
    color: black;
}

.marketplace-header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.post-board .marketplace-header img {
    display: block;
    margin: 10px;
}

.marketplace-header {
    height: 100px;
}


/* SEARCHBAR */
.search {
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
}

.searchbar {
    display: grid;
    grid-template-columns: 17fr 3fr;
    width: 100%;
}

.searchbar input {
    width: 100%;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    padding-left: 20px;
    outline: none;
    font-size: 1.4rem;
}

#marketplace svg.MuiSvgIcon-root.searchIcon.MuiSvgIcon-colorPrimary {
    margin-left: 10px;
}

#marketplace input#searchbar.MuiOutlinedInput-inputAdornedStart {
    padding-left: 10px;
    color: black;
}

.matches {
    justify-self: center;
}

/* POSTS */
#marketplace .post-board {
    background-color: grey;
    box-shadow: 1px 1px 5px darkgrey;
    display: grid;
    grid-template-rows: 1fr;
    grid-gap: 8px;
    padding: 8px;

    border-radius: 8px;
}

#marketplace .post-board .post {
    display: flex;
    flex-direction: column;
    
    min-height: 200px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 1px 1px 5px black;

}

#marketplace .post-header {
    background-color: grey;
    color: black;
    box-shadow: 1px 1px 5px black;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    padding: 12px;

    display: flex;
    flex-direction: column;
}

#marketplace .post-title-container {
    display: flex;
    justify-content: space-between;
}

#marketplace .post-content {
    padding: 20px 10px 20px 10px;
    display: flex;
    justify-content: space-between;
    font-weight: 100;
}

#marketplace .post-content p {
    border-left: 2px solid rgb(199, 199, 199);
    padding: 0 10px 0 10px;
}

#marketplace .post-arrow-container {
    display: flex;
    width: 60px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#marketplace .post-arrow-container .MuiSvgIcon-root {
    width: 2rem;
    height: 2rem;
}


/* NEW POST FORM */
.new-post {
    position: absolute;
    right: 0;
    background-color: white;
    box-shadow: 1px 1px 5px black;
    border-top-left-radius: 8px;
    height: 100%;
    width: 50vw;
    padding: 30px;
    animation: slideIn;
    animation-duration: 0.3s;
}

@keyframes slideIn {
    from {right: -1000px}
    to {right: 0}
}

.new-post-header {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 20px 0;
    font-size: 1.2rem;
}

.new-post, .new-post-form {
    display: flex;
    flex-direction: column;
}

.new-post-form {
    width: 100%;
}


/* MOBILE MEDIA QUERIES */
@media screen and (max-width: 768px) {

    .header-container img {
        width: 300px;
    }

    .new-post {
        width: 80vw;
    }

    .post-header h1, .post-header h2, .post-header h3 {
        font-size: 1.2rem;
    }

}

@media screen and (max-width: 500px) {

    #marketplace {
        padding: 8px;
    }

    .marketplace-header{
        height: 18vw;
    }

    .post-header h1, .post-header h2, .post-header h3 {
        font-size: 1rem;
    }
    .marketplace-header-container {
        justify-content: center;
    }
    
}
#post-view {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
    padding: 30px;
    margin-top: 100px;
}

#post-view a {
    text-decoration: none;
}

#post-view .back {
    height: 50px;
}

#post-view .single-post {
    display: flex;
    flex-direction: column;
    min-height: 200px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 1px 1px 5px black;
}

.single-post-header {
    padding: 20px;
    background-color: #ef6c00;
    color: white;
    box-shadow: 1px 1px 5px black;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.single-post-body {
    display: flex;
    justify-content: space-between;
}

.single-post-content {
    grid-row: 2;
    padding: 0 20px 20px 20px;
}

.data-pair {
    display: grid;
    grid-template-columns: 120px 1fr;
    margin: 20px 0 20px 0;
}

.data-pair .data-type {
    margin-bottom: 5px;
}

.data {
    font-weight: 100;
    border-left: 2px solid rgb(199, 199, 199);
    padding-left: 20px;
}


/* ACTIONS SECTION */
.actions-section {
    display: flex;
    padding: 20px 10px 20px 10px;
}

.actions-section button {
    margin: 0 8px 0 8px;
    width: 140px;
    height: 50px;
}

.unauthorized-disclaimer, .message-sent-disclaimer {
    font-style: italic;
    font-weight: 100;
    display: block;
    margin: 0 20px 20px 20px;
}

/* MESSAGE SECTION */
.message-section {
    padding: 20px;
}

.message-section .buttons-section {
    display: flex;
    justify-content: space-between;
}

.post-view-messages {
    display: flex;
    flex-direction: column;
    padding: 0 12px 10px 12px;
}

.my-messages-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-gap: 8px;
    background-color: rgb(235, 235, 235);
}

.my-message-list {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-gap: 8px;
    padding: 8px;

    max-height: 65vh;
    min-height: 200px;
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.my-messages-container .message {
    box-shadow: 1px 1px 5px black;
    border-radius: 8px;
    background-color: white;
}


/* EDITOR */
.post-editor {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: white;
    box-shadow: 1px 1px 5px black;
    border-top-left-radius: 8px;
    width: 50vw;
    padding: 30px;
    animation: slideIn;
    animation-duration: 0.3s;
}

@keyframes slideIn {
    from {right: -1000px}
    to {right: 0}
}

.editor-header {
    display: flex;
    justify-content: space-between;
    margin: 20px 0 20px 0;
    font-size: 1.2rem;
}

.post-editor-form {
    display: flex;
    flex-direction: column;
}

.new-post-form {
    width: 100%;
}


/* CONFIRMATION WINDOWS */
.confirmation-window {
    position: fixed;
    top: 30vh;
    right: 30vw;
    left: 30vw;
    border-radius: 8px;
    background-color: white;
    box-shadow: 1px 1px 5px black;
    z-index: 100;
}

.confirmation-window.delete {
    right: 45vw;
}

.confirmation-window h3, .confirmation-window p, .confirmation-window .confirmation-buttons {
    margin: 30px;
}

.confirmation-window .confirmation-buttons {
    display: flex;
}

.confirmation-window .confirmation-buttons button {
    margin: 0px 10px 0px 10px;
    width: 120px;
}


/* MOBILE MEDIA QUERIES */
@media screen and (max-width: 600px) {
    .post-editor {
        position: absolute;
        right: 0;
        top: 100px;
        width: 100vw;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        padding: 30px;
        animation: slideIn;
        animation-duration: 0.3s;
    }

    .confirmation-window {
        top: 30vh;
        right: 10vw;
        left: 10vw;
    }

    .confirmation-window.delete {
        right: 10vw;
    }
}
#account {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
    padding: 30px;
    margin-top: 100px;
}

.welcome {
    color: white;
}

.account-container, .account-body {
    grid-column: 1;
}

.account-header-container {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.account-header img {
    display: block;
    margin: 10px;
}

.account-header {
    height: 90px;
}


/* MAIN BODY STYLES */
.account-body {
    display: grid;
    grid-template-rows: 100px 1fr;
    grid-template-columns: 200px 1fr;
    flex-wrap: wrap;
    background-color: #333;
    box-shadow: 1px 1px 5px black;
    border-radius: 8px;
}

.welcome-user {
    grid-row: 1;
    grid-column: 1 / 3;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.account-body .welcome-user h1 {
    font-size: 2.4rem;
}

.account-nav-buttons, .user-messages, .user-posts {
    grid-row: 2;
}

/* ACCOUNT CENTER NAV BUTTONS */
.account-center-nav {
    width: 200px;
    height: 60px;
    outline: none;
    border: none;
    font-size: 1.2rem;
    color: white;
    background-color: grey;
    box-shadow: -1px -1px 5px black;
    cursor: pointer;
}

.account-nav-buttons {
    display: flex;
    flex-direction: column;
    background-color: #333;
}

.account-center-nav.nav-tab-active {
    background-color: #ef6c00;
}


/* ACTIVE DISPLAY SECTION (POST / MESSAGES) */
.user-posts, .user-messages {
    background-color: grey;
    box-shadow: 1px 1px 5px black;
    display: flex;
    flex-direction: column;
    padding: 8px;
    border-radius: 0 0 8px 8px;
    width: 100%;
}

.section-header {
    height: 60px;
}

.account-body h1 {
    font-size: 2rem;
    margin: 8px 20px 8px 20px;
}

#account .section-header h1 {
    color: white;
}

.message-list, .post-list {
    margin-left: 20px;
    background-color: grey;
    height: 65vh;
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    border-radius: 0 8px 8px 8px;
}

.messageboard-tabs {
    margin-left: 20px;
}

.messageboard-tab {
    height: 40px;
    width: 100px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    outline: none;
    border: none;
    background-color: #ef6c00;
    color: black;
    cursor: pointer;
    border: 1px solid black;
}

.messageboard-tab.tab-active {
    background-color: #ef6c00;
    border: 1px solid black;
}   

.user-posts .post, .user-messages .message {
    background-color: white;
    margin: 8px;
    border-radius: 8px;
    box-shadow: 1px 1px 5px black;
}


/* POSTS */
#account .post {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 8px;
    box-shadow: 1px 1px 5px black;
}

#account .post-header {
    background-color: #ef6c00;
    color: black;
    box-shadow: 1px 1px 5px black;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    padding: 12px;

    display: flex;
    flex-direction: column;
}

#account .post-title-container {
    display: flex;
    justify-content: space-between;
}

#account .post-content {
    padding: 20px 10px 20px 10px;
    display: flex;
    justify-content: space-between;
    font-weight: 100;
}

#account .post-content p {
    border-left: 2px solid rgb(199, 199, 199);
    padding: 0 10px 0 10px;
}

#account .post-arrow-container {
    display: flex;
    width: 60px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#account .post-arrow-container .MuiSvgIcon-root {
    width: 2rem;
    height: 2rem;
}

#account .post.expired-post {
    color: #999;
}

#account .post.expired-post .post-header {
    background-color: #999;
}


/* MESSAGE CENTER */
.message-header {
    background-color: #333;
    color: black;
    box-shadow: 1px 1px 5px black;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    padding: 12px;

    display: flex;
    flex-direction: column;
}

.message-content {
    display: flex;
    justify-content: space-between;
    font-weight: 100;
    padding: 20px;
}

.message-content p {
    border-left: 2px solid rgb(199, 199, 199);
    padding: 0 10px 0 10px;
}

.message-content .respond-button-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.empty-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
    align-items: center;
    height: 100%;
    font-size: 1.6rem;
    color: white;
    opacity: 70%;
}

.empty-list img {
    height: 50px;
    margin: 20px;
}


/* MOBILE MEDIA QUERIES */
@media screen and (max-width: 600px) {

    #account {
        padding: 8px;
    }

    .account-header-container h1 {
        display: flex;
        justify-content: center;
        margin-bottom: 12px;
    }

    .account-header{
        height: 16vw;
    }

    .account-body {
        grid-template-rows: 100px 60px 1fr;
        grid-template-columns: 200px 1fr;
        flex-wrap: wrap;
        background-color: grey;
        box-shadow: 1px 1px 5px black;
        border-radius: 8px;
    }
      
    .account-body .welcome-user h1 {
        font-size: 2rem;
    }
    
    .account-nav-buttons {
        grid-column: 1 / 3;
        flex-direction: row;
    }
    .account-center-nav {
        box-shadow: 0 0 2px black;
    }
    .user-messages, .user-posts {
        grid-row: 3;
        grid-column: 1 / 3;
    }
    .section-header {
        height: 80px;
    }

    .section-header h1 {
        margin: 8px 0 8px 0;
    }

    .post-list, .message-list, .messageboard-tabs{
        margin-left: 0;
    }
}
